.search-form {
  background-color: #4253af;
  justify-content: center;
  padding-top: 20px;
  padding-bottom: 20px;
  display: flex;
}

.gallery {
  grid-template-columns: repeat(4, 1fr);
  gap: 10px;
  padding: 20px 10px;
  display: grid;
}

.photo-card {
  background-color: rgba(10, 10, 10, .04);
  border-radius: 3px;
  padding: 15px;
  box-shadow: 0 0 5px rgba(10, 10, 10, .4);
}

img {
  width: 100%;
}

.info {
  justify-content: space-around;
  display: flex;
}

.info-item {
  flex-direction: column;
  align-items: center;
  display: flex;
}

.load-more {
  color: #fff;
  background-color: rgba(66, 83, 175, .9);
  border: none;
  border-radius: 2px;
  margin-left: auto;
  margin-right: auto;
  padding: 5px;
  display: none;
}

.hidden {
  display: block;
}

